body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 1rem; }

.container-fluid {
  @media screen and ( min-width: 576px ) {
    width: calc( 100% - 4rem ); }
  @media screen and ( min-width: 768px ) {
    width: calc( 100% - 8rem ); }
  @media screen and ( min-width: 992px ) {
    width: calc( 100% - 12rem ); } }

.bg_gradient {
  &_1 {
    background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%); }
  &_2 {
    background-image: linear-gradient(-225deg, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%); }
  &_3 {
    background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%); }
  &_4 {
    background-image: linear-gradient(to top, #4481eb 0%, #04befe 100%); }
  &_facebook {
    background-image: linear-gradient(to top, #0062e0, #19afff); } }

.bg__caothang {
  background: #283790; }

.bg__caothang__light {
  background: #2191F2; }

.fg__caothang {
  color: #283790; }

.bg__image {
  &__1 {
    background: url('../img/backgrounds/bg__image__1.jpg') center center fixed;
    background-size: cover; }
  &__2 {
    background: url('../img/backgrounds/bg__image__2.jpg') center center fixed;
    background-size: cover; } }


@import 'header/index.sass';
